heml, body {
  width: 100%;
  font-size: 16px;
}

.app-content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  -ms-flex-align: center;
      align-items: center;
  padding: 1rem 0;
  text-align: left;
}

.app-content .wrapper {
  width: 500px;
  overflow: hidden;
}

@media screen and (max-width: 500px) {
  .app-content .wrapper {
    width: 90%;
  }
}

input {
  font-size: 15px;
  padding: 5px;
  height: 1.1rem;
}

button {
  font-size: 15px;
  padding: 5px;
  color: #222222;
  margin-left: 5px;
  max-height: 2.5rem;
}

button.del-completed {
  margin: 0;
}

button.del-completed:disabled {
  opacity: 0.5;
}

.red {
  color: #ff0000;
}


